import React, {useEffect} from 'react';
import {useSelector} from 'react-redux';
import CustomTextField from '../../../../Layout/CustomTextField';

function AustralianBankAccount({onChangeFn}) {
    const secB = useSelector((store) => store.secB)
    const invFormStatus = useSelector((store) => store.invFormStatus)

    useEffect(()=>{
        return ()=>{
            onChangeFn({name:'bsb', value:""})
            onChangeFn({name:'account_number', value:""})

        }
    },[])

  return (
    <div className="sub__form">
        <CustomTextField 
            placeholder="BSB"
            id="sec-b-bsb"
            label="BSB"
            type=""
            asterisk="*"
            name="bsb"
            value={secB?.bsb || ""}
            onChangeFn={onChangeFn}
            formErrors={invFormStatus.errors}
        />

        <CustomTextField 
            placeholder="Account Number"
            id="sec-b-account-number"
            label="Account Number"
            type=""
            asterisk="*"
            name="account_number"
            value={secB?.account_number || ""}
            onChangeFn={onChangeFn}
            formErrors={invFormStatus.errors}
        />
    </div>
  )
}

export default AustralianBankAccount