import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import CustomTextField from '../../../Layout/CustomTextField';


function OtherFundSource({handleInput}) {
    const secB = useSelector((store) => store.secB);
    const invFormStatus = useSelector((store) => store.invFormStatus)

    useEffect(()=>{
        return ()=>{
            handleInput({name:'other_fund_source_specification', value:''})
        }
    },[])

    return (
    <div> 
        <CustomTextField 
            placeholder="Please specify other Source of Funds"
            id="other-source-of-funds"
            label="Please specify"
            asterisk="*"
            type="form-input-full-width"
            name="other_fund_source_specification"
            value={secB?.other_fund_source_specification || ""}
            onChangeFn={handleInput}
            formErrors={invFormStatus.errors}
        />
    </div>
  )
}

export default OtherFundSource