import React, { useState } from 'react';
import {useSelector} from 'react-redux'
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import DocPlaceholder from '../../../Layout/DocPlaceholder';
import { POWER_OF_ATTORNEY_NO, POWER_OF_ATTORNEY_YES, LOADING, SUCCESS, FAILED, POWER_OF_ATTORNEY_DOCUMENTS } from '../../../../../constants';
import { uploadFiles, deleteFile } from '../../../../../actions/Investor-Application-Form/uploadDocActions';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import CustomDropdown from '../../../Layout/CustomDropdown';
import { addNewDocToArray, removeDocFromArray, updateDocArray } from '../../../../../utils/documentUploads';
import { useToast } from '../../../../CustomToast';

const PowerOfAttorney = ({ onFormChange, investorFormId }) => {
    const toast = useToast();
    const secB = useSelector((store) => store.secB);
    const invFormStatus = useSelector((store) => store.invFormStatus);

    const [selectedDocType, setSelectedDocType] = useState({label:"Select type of document", value:""})
    const [uploadedDocs, setUploadedDocs] = useState([]);

    const onChangeFn = ({ name, value }) =>{ 
        const convrtToNum = parseInt(value);
        onFormChange({ name, value: convrtToNum });
    }

    const onUploadFile = async(file, type={label:'', value:''}) =>{ 
        if(file){ //files && files.length -- to make it work for multiple files
            //call api to upload files
            const fileName = file.name;                 
            const formData = new FormData();

            formData.append('file', file);
            formData.append('type', type.value);
            formData.append('section', 'b');
            formData.append('investor_form_id', investorFormId);

            const { success, url, data } = await uploadFiles(formData);
            const extradata = {
                loader: success? SUCCESS : FAILED,
                docUrl: url,
                resData: data
            }
            updateDocArray({setUploadedDocs, fileName, extradata})
        }
    }

    const onAddFiles = (files) => {
        //add selected files(by user) to local state - and call api
        addNewDocToArray({
            files,
            uploadedDocs,
            setUploadedDocs,
            onUploadFile,
            selectedDocType:{...selectedDocType},
        })
    }

    const onClickRemove = (fileName) => {
        //just remove the file from local state
        removeDocFromArray({fileName, uploadedDocs, setUploadedDocs})
    }

    const updateSelectedOption = (option) => {
        //update selected document type to the state
        setSelectedDocType({...option})
    }

    const onClickDel = async({fileName, docData}) => {
        //delete the file from aws & remove the file from local state
        updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: true}})
        const { success } = await deleteFile(docData?.resData);
        success && onClickRemove(fileName);
        success && toast.success("Deleted successfully!");
        success || updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: false}})
        success || toast.error("Please try again!")
    }
    
  return (
    <div className="section__container">
        <h4 className="section__container--subtitle" >B.2 Power Of Attorney</h4>
        <div className="section__container--box">
            <div>
                <CustomRadioInput 
                    name="poa"
                    id="have-appointed-POA"
                    content="I/we have appointed a Power of Attorney (POA) to act on my/our behalf"
                    val={POWER_OF_ATTORNEY_YES}
                    checked={secB.poa === POWER_OF_ATTORNEY_YES}
                    onChangeFn={onChangeFn}
                />

                <CustomRadioInput 
                    name="poa"
                    id="not-applicable"
                    content="Not Applicable"
                    val={POWER_OF_ATTORNEY_NO}
                    checked={secB.poa === POWER_OF_ATTORNEY_NO}
                    onChangeFn={onChangeFn}
                />
                {invFormStatus.errors?.poa? <div className="input__desc">
                    {renderErrorTxt(invFormStatus.errors?.poa)}
                </div>: null}
            </div>

            {secB.poa === POWER_OF_ATTORNEY_YES? <div className="u-padding-y">
                <h6 className="u-margin-bottom u-padding-y">Please provide an original certified copy of the POA and certified identification documents of the attorney<span>*</span></h6>
                <CustomDropdown
                    dropdownOptions={POWER_OF_ATTORNEY_DOCUMENTS}
                    selectedOption={selectedDocType}
                    updateSelectedOption={updateSelectedOption}
                />
                {selectedDocType?.value ?
                    <div className='u-padding-y'> 
                        <DocPlaceholder onAddFiles={onAddFiles} uploadedDocs={uploadedDocs} onClickRemove={onClickRemove} onClickDel={onClickDel} uniqId="powerofattorney"/>
                    </div>: null
                } 
            </div>: null}
        </div>            
    </div>
  )
}

export default PowerOfAttorney;