import React, {useEffect} from 'react';
import {useSelector} from 'react-redux';
import CustomTextField from '../../../../Layout/CustomTextField';

function ForiegnBankAccount({onChangeFn}) {
    const secB = useSelector((store) => store.secB)
    const invFormStatus = useSelector((store) => store.invFormStatus)

    useEffect(()=>{
        return ()=>{
            onChangeFn({name:'institution_name', value:""})
            onChangeFn({name:'bank_branch_address', value:""})
            onChangeFn({name:'swift', value:""})
            onChangeFn({name:'account_number', value:""})
        }
    },[])

    return (
    <div>               
        <CustomTextField
            placeholder="Name of financial institution"
            id="sec-b-intl-fin-institute"
            label="Name of financial institution"
            asterisk="*"
            type="form-input-full-width"
            name="institution_name"
            value={secB?.institution_name || ""}
            onChangeFn={onChangeFn}
            formErrors={invFormStatus.errors}
        />

        <CustomTextField
            placeholder="Bank branch address"
            id="sec-b-bank-branch-address"
            label="Bank branch address"
            asterisk="*"
            type="form-input-full-width"
            name="bank_branch_address"
            value={secB?.bank_branch_address || ""}
            onChangeFn={onChangeFn}                        
            formErrors={invFormStatus.errors}
        />

        <div className="sub__form">
            <CustomTextField 
                placeholder="SWIFT"
                id="sec-b-intl-bank-swift"
                label="SWIFT"
                asterisk="*"
                name="swift"
                value={secB?.swift || ""}
                onChangeFn={onChangeFn}
                formErrors={invFormStatus.errors}
            />
            <CustomTextField 
                placeholder="Account number"
                id="sec-b-intl-account-number"
                label="Account number"
                asterisk="*"
                name="account_number"
                value={secB?.account_number || ""}
                onChangeFn={onChangeFn}
                formErrors={invFormStatus.errors}
            />
        </div>
    </div>
  )
}

export default ForiegnBankAccount