import React, {useEffect} from 'react';
import {useSelector} from 'react-redux';
import CustomTextField from '../../../../Layout/CustomTextField';

function AccountNameInp({onChangeFn}) {
    const secB = useSelector((store) => store.secB)
    const invFormStatus = useSelector((store) => store.invFormStatus)

    useEffect(()=>{
        return ()=>{
            onChangeFn({name:'account_name', value:""})
        }
    },[])

  return (
    <CustomTextField
        placeholder="Account name"
        id="sec-b-account-name"
        label="Account name"
        type="form-input-full-width"
        asterisk="*"
        belowText="The nominated bank account must be in the name of the investor (or where joint investors, in the name of at least one of the investors)."
        name="account_name"
        value={secB?.account_name || ""}
        onChangeFn={onChangeFn}
        formErrors={invFormStatus.errors}
    />
  )
}

export default AccountNameInp