import React from 'react';
import { useSelector } from 'react-redux';
import NominatedBankAccountDetails from './SubSections/NominatedBankAccountDetails';
import PrimaryContact from './SubSections/PrimaryContact';
import PowerOfAttorney from './SubSections/PowerOfAttorney';
import SourceOfFunds from './SubSections/SourceOfFunds';
import Backdrop from '../../Layout/Backdrop';
import {formatAndPrintDocs} from '../../../../utils/validations/helperFunc';

const SectionBContent = ({ onFormChange, investorFormId, removeDocFromArr }) => {

  const secB = useSelector((store) => store.secB);
  const invFormStatus = useSelector((store) => store.invFormStatus);

  return (
    <div className="section">
        <h2 className="section__heading">Section B: Your Contact Details</h2>
        <ul>
            <li>All investors must complete this section.</li>
            <li>All fields in Section A MUST be completed.</li>
        </ul>

        <PrimaryContact onFormChange={onFormChange}/> 

        <PowerOfAttorney onFormChange={onFormChange} investorFormId={investorFormId}/>

        <NominatedBankAccountDetails onFormChange={onFormChange} investorFormId={investorFormId}/>

        <SourceOfFunds onFormChange={onFormChange}/>

        {secB?.documents?.length > 0? formatAndPrintDocs(secB?.documents, removeDocFromArr) : null}
    </div>
  )
}

export default SectionBContent;