import React, { useMemo } from 'react';
import { useSelector } from 'react-redux'
import Select from 'react-select';
import CustomTextField from '../../../Layout/CustomTextField';
import { isNumber } from '../../../../../utils/regexFuncs';
import countryList from 'react-select-country-list';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
// import CustomTextButtonField from '../../../Layout/CustomTextButtonField';

const PrimaryContact = ({onFormChange}) => {

    const secB = useSelector((store) => store.secB)
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const options = useMemo(() => countryList().getData(), []);

    const countryValue = () => {
        const value = options.find((country) => {
            if(secB?.country){
              return  country.label?.toLowerCase() === secB?.country?.toLowerCase()
            }
            return false
        });
        return value || null;
    }

    const onChangeSelectHandler = (value) => {
        onFormChange({ name: 'country', value: value.label })
    }

    const onChangeFn = ({name, value}) =>{
        if(name === 'phone' || name ==='mobile_number'){
            const isValid = value==='' || isNumber(value);
            if(!isValid) return
        }

        onFormChange({name, value})
    }
    

  return (
    <div className="section__container">
        <h4 className="section__container--subtitle">B.1 Primary Contact</h4>

        {/* section container box */}
        <div className="section__container--box">
            <p className="text__box--p">Please provide details of key contact person for correspondence.</p>
            
            <div className="note__box">
                <p className="u-margin-bottom"><span>Note:</span> A contact person is only entitled to receive information or make enquiries only. A contact person has no authority to make any changes to the account. Should you wish to add an Authorised Person to operate your account, please complete a <a href="http://silcgroup.com/wp-content/uploads/2021/01/Change-of-Details-Form-New.pdf" target="_blank" rel="noreferrer">change of details form</a></p>
            </div>

            <div className="sub__form">

                <CustomTextField 
                    id="sec-b-title"
                    placeholder="Title"
                    label="Title"
                    asterisk="" 
                    name="primary_contact_title"
                    value={secB?.primary_contact_title || ""}
                    onChangeFn={onChangeFn}
                    formErrors={invFormStatus.errors}
                />
                
                <CustomTextField 
                    id="sec-b-given-name"
                    placeholder="Given Name"
                    label="Given Name(s)"
                    asterisk="*"
                    name="primary_contact_name"
                    value={secB?.primary_contact_name || ""}
                    onChangeFn={onChangeFn}
                    formErrors={invFormStatus.errors}
                />

                <CustomTextField 
                    id="sec-b-middle-name"
                    placeholder="Middle Name"
                    label="Middle Name"
                    asterisk=""
                    name="primary_contact_middle_name"
                    value={secB?.primary_contact_middle_name || ""}
                    onChangeFn={onChangeFn}
                    formErrors={invFormStatus.errors}
                />

                <CustomTextField 
                    id="sec-b-surname"
                    placeholder="Surname"
                    label="Surname"
                    asterisk="*"
                    name="primary_contact_surname"
                    value={secB?.primary_contact_surname || ""}
                    onChangeFn={onChangeFn}
                    formErrors={invFormStatus.errors}
                />
            </div>

            <div>
                <h5 className="u-margin-bottom-m">Contact Details</h5>

                <div className="sub__form">
                    
                    <CustomTextField
                        inputType="tel"
                        id="sec-b-phone"
                        placeholder="Phone"
                        label="Phone"
                        asterisk="*"
                        // belowText="If non-Australian phone number, please include international calling code e.g. +60 12 9600282"
                        name="phone"
                        value={secB?.phone || ""}
                        onChangeFn={onChangeFn}
                        formErrors={invFormStatus.errors}
                    />

                    <CustomTextField
                        inputType="tel"
                        id="sec-b-mobile"
                        placeholder="Mobile"
                        label="Mobile / Other"
                        asterisk=""
                        name="mobile_number"
                        value={secB?.mobile_number || ""}
                        onChangeFn={onChangeFn}
                        formErrors={invFormStatus.errors}
                    />

                    <CustomTextField 
                        id="sec-b-primary-email" 
                        placeholder="Enter your email"
                        type="email"
                        inputType="email"
                        label="Primary Email"
                        asterisk="*"
                        fullWidth=""
                        name="primary_email"
                        value={secB?.primary_email || ""}
                        onChangeFn={onChangeFn} 
                        formErrors={invFormStatus.errors}
                    />

                    <CustomTextField 
                        placeholder="Alternative or Advisor Email" 
                        id="sec-b-advisor-email" 
                        label="Alternative or Advisor Email" 
                        inputType="email"
                        asterisk=""
                        name="alternative_email"
                        value={secB?.alternative_email || ""}
                        onChangeFn={onChangeFn}
                        formErrors={invFormStatus.errors}
                    />
                    
                    <div className="note__box u-padding-10y u-margin-bottom-xl">
                        <p><span>Note:</span> we are committed to reducing our ecological footprint and our preferred method of communication is by e-mail. By providing this e-mail address, you consent for us to communicate with you using these e-mail addresses unless otherwise notified to us in writing.</p>
                    </div>
                </div>
            </div>

            <div>
                <h5 className="u-margin-bottom-m">Postal Address</h5>
                <div className="sub__form">

                    <CustomTextField 
                        id="sec-b-unit"
                        label="Unit No./Level"
                        placeholder="Unit No./Level"
                        asterisk=""
                        // type="form-input-full-width"
                        name="unit_number"
                        value={secB?.unit_number || ""}
                        onChangeFn={onChangeFn}
                        formErrors={invFormStatus.errors}
                    />

                    <CustomTextField 
                        id="sec-b-street-num"
                        label="Street Number"
                        placeholder="Street Number"
                        asterisk=""
                        // type="form-input-full-width"
                        name="street_no"
                        value={secB?.street_no || ""}
                        onChangeFn={onChangeFn}
                        formErrors={invFormStatus.errors}
                    />

                    <CustomTextField 
                        id="sec-b-suburb"
                        label="Street Name"
                        placeholder="Street Name"
                        asterisk="*"
                        // type="form-input-full-width"
                        name="street_name"
                        value={secB?.street_name || ""}
                        onChangeFn={onChangeFn}
                        formErrors={invFormStatus.errors}
                    />

                    <CustomTextField 
                        id="sec-b-suburb"
                        label="Suburb/Town"
                        placeholder="Suburb/Town"
                        asterisk="*"
                        name="suburb"
                        value={secB?.suburb || ""}
                        onChangeFn={onChangeFn}
                        formErrors={invFormStatus.errors}
                    /> 

                    <CustomTextField 
                        id="sec-b-state"
                        label="State"
                        placeholder="State"
                        asterisk="*"
                        name="state"
                        value={secB?.state || ""}
                        onChangeFn={onChangeFn}
                        formErrors={invFormStatus.errors}
                    />

                    <CustomTextField 
                        id="sec-b-postcode"
                        label="Postcode"
                        placeholder="Postcode"
                        asterisk="*"
                        name="postcode"
                        value={secB?.postcode || ""}
                        onChangeFn={onChangeFn}
                        formErrors={invFormStatus.errors}
                    />

                    <div className="country__dropdown">
                        <label>Country <span>*</span></label>
                        <Select
                            className={invFormStatus.errors?.country && 'incorrect'}
                            id="select-dropdown-country"
                            name="country"
                            autoFocus={false}
                            options={options}
                            placeholder="Select a Country"
                            value={countryValue()}
                            onChange={onChangeSelectHandler}
                        />

                        {invFormStatus.errors?.country?
                        <div className="input__desc">
                            {renderErrorTxt(invFormStatus.errors?.country)}
                        </div>: null}
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default PrimaryContact;