import React, { useState } from 'react';
import {useSelector} from 'react-redux'
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import DocPlaceholder from '../../../Layout/DocPlaceholder';
import { AUSTRALIAN_BANK_ACCOUNT, FOREIGN_BANK_ACCOUNT, LOADING, SUCCESS, FAILED, WILL_PROVIDE_LATER, NOMINATED_BANK_ACCOUNT_DETAILS_DOCUMENTS } from '../../../../../constants';
import { uploadFiles, deleteFile } from '../../../../../actions/Investor-Application-Form/uploadDocActions';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import AccountNameInp from './bankAccount/AccountNameInp';
import AustralianBankAccount from './bankAccount/AustralianBankAccount';
import ForiegnBankAccount from './bankAccount/ForiegnBankAccount';
import CustomDropdown from '../../../Layout/CustomDropdown';
import { addNewDocToArray, removeDocFromArray, updateDocArray } from '../../../../../utils/documentUploads';
import { useToast } from '../../../../CustomToast';

const NominatedBankAccountDetails = ({onFormChange, investorFormId}) => {
    const toast = useToast();
    const secB = useSelector((store) => store.secB)
    const invFormStatus = useSelector((store) => store.invFormStatus)
    const [selectedDocType, setSelectedDocType] = useState({label:"Select type of document", value:""})

    const [uploadedDocs, setUploadedDocs] = useState([])
    const onChangeFn = ({name, value}) =>{
        onFormChange({name, value});
    }

    const onChangeRadioFn = ({name, value}) =>{
        const convrtToNum = parseInt(value);
        onFormChange({name, value:convrtToNum});
    }

    const onUploadFile = async(file, type={label:'', value:''}) =>{ //call api to upload file
        if(file){                    
            const fileName = file.name                   
            const formData = new FormData();
            formData.append('file', file);
            formData.append('type', type.value);
            formData.append('section', 'b');
            formData.append('investor_form_id', investorFormId);
            const {success, url, data} = await uploadFiles(formData);
            const extradata = {
                loader: success? SUCCESS : FAILED,
                docUrl: url,
                resData: data
            }
            updateDocArray({setUploadedDocs, fileName, extradata})
        }
    }

    const onAddFiles = (files) => {
        //add selected files(by user) to local state - and call api
        addNewDocToArray({
            files,
            uploadedDocs,
            setUploadedDocs,
            onUploadFile,
            selectedDocType:{...selectedDocType},
        })
    }

    const onClickRemove = (fileName) => {
        //just remove the file from local state
        removeDocFromArray({fileName, uploadedDocs, setUploadedDocs})
    }

    const updateSelectedOption = (option) => {
        //update selected document type to the state
        setSelectedDocType({...option})
    }

    const onClickDel = async({fileName, docData}) => {
        //delete the file from aws & remove the file from local state
        updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: true}})
        const { success } = await deleteFile(docData?.resData);
        success && onClickRemove(fileName);
        success && toast.success("Deleted successfully!");
        success || updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: false}})
        success || toast.error("Please try again!")
    }

  return (
    <div className="section__container">
        <h4 className="section__container--subtitle">B.3 Nominated Bank Account Details</h4>

        <div className="section__container--box terms">
            <p className="text__box--p">Please provide details of the account into which you would like distributions and redemptions to be paid.</p>

            <div className="note__box u-padding-10y u-margin-bottom-0">
                <p className="note__box--p">By providing your nominated bank account, you authorise the Trustee to use these details for all future transaction requests that you make including credits in relation to any withdrawal proceeds until written notice is provided otherwise. Please check these details carefully as it is your responsibility to ensure all payee account details are correct. Incorrect details may result in loss of funds and we do not accept liability for loss of funds due to incorrect or incomplete bank account details provided by you.</p>
            </div>

            <div className="note__box u-padding-10y u-margin-bottom-0">
                <p className="note__box--p">All distributions will be paid in Australian dollars (unless otherwise specified in the IM). If an international (non-Australian) bank account is provided, it must be at a bank where the investor is a tax resident and any applicable bank fees will be deducted from the payment.</p>
            </div>

            <div className="u-margin-bottom-lg u-padding-10y">
                <CustomRadioInput 
                    id="australian-bank-account"
                    name="account_origin"
                    content="Australian Bank Account"
                    val={AUSTRALIAN_BANK_ACCOUNT}
                    checked={secB.account_origin === AUSTRALIAN_BANK_ACCOUNT}
                    onChangeFn={onChangeRadioFn}
                />

                <CustomRadioInput 
                    id="intl-bank-account"
                    name="account_origin"
                    content="International / Foreign Currency Bank Account"
                    val={FOREIGN_BANK_ACCOUNT}
                    checked={secB.account_origin === FOREIGN_BANK_ACCOUNT}
                    onChangeFn={onChangeRadioFn}
                />

                <CustomRadioInput 
                    id="provide-later"
                    name="account_origin"
                    content="I will provide later"
                    val={WILL_PROVIDE_LATER}
                    checked={secB.account_origin === WILL_PROVIDE_LATER}
                    onChangeFn={onChangeRadioFn}
                />

                {invFormStatus.errors?.account_origin? <div className="input__desc">
                    {renderErrorTxt(invFormStatus.errors?.account_origin)}
                </div>: null}
                </div>

            {(secB.account_origin || secB.account_origin === AUSTRALIAN_BANK_ACCOUNT) && secB.account_origin !== 2?//extra condition to manage value 0
                <div>
                    <AccountNameInp onChangeFn={onChangeFn}/>

                    {secB.account_origin === AUSTRALIAN_BANK_ACCOUNT? <AustralianBankAccount onChangeFn={onChangeFn}/>: null}

                    {secB.account_origin === FOREIGN_BANK_ACCOUNT? <ForiegnBankAccount onChangeFn={onChangeFn}/> : null}
                </div>
            : null}

            <div className="u-padding-10y">
                <h6 className="u-margin-bottom u-padding-y">Please upload a copy of your bank statement showing bank account name and account number</h6>
                <CustomDropdown
                    dropdownOptions={NOMINATED_BANK_ACCOUNT_DETAILS_DOCUMENTS}
                    selectedOption={selectedDocType}
                    updateSelectedOption={updateSelectedOption}
                />
                {selectedDocType?.value ?
                    <div className='u-padding-y'> 
                        <DocPlaceholder
                            onAddFiles={onAddFiles}
                            uploadedDocs={uploadedDocs}
                            onClickRemove={onClickRemove}
                            onClickDel={onClickDel}
                            uniqId="nominatedbankaccountdetails"
                        />
                    </div>: null
                }
            </div>

        </div>
    </div>
  )
}

export default NominatedBankAccountDetails;